<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <el-form :model="loginForm" status-icon :rules="rules2" ref="loginForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="BusiCode">
        <el-input v-model.number="loginForm.BusiCode"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model.number="loginForm.phone"></el-input>
      </el-form-item>
      <el-form-item label="" prop="pass">
        <el-input type="password" v-model="loginForm.pass" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('loginForm')"></el-button>
      </el-form-item>
    </el-form>
    <ul>
      <router-link tag="li" to="/register"></router-link>
      <router-link tag="li" to="/forgetpass"></router-link>
    </ul>
  </div>
</template>

<script>
import { isValidMobile, isValidBusiCode } from '@/util/extends.js'
export default {
    data() {
      var validPhone=(rule, value,callback)=>{
        if (!value){
            callback(new Error(''))
        }else  if (!isValidMobile(value)){
          callback(new Error('11'))
        }else {
            callback()
        }
      }
      var validBusiCode=(rule, value,callback)=>{
          if (!value){
              callback(new Error(''))
          }else  if (!isValidBusiCode(value)){
            callback(new Error(''))
          }else {
              callback()
          }
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        }  else{
          callback();
        }
      }
      return {
        msg: '',
        loginForm: {
          pass: '',
          phone: '',
          BusiCode: ''
        },
        rules2: {
          pass: [
            {required: true, min: 8, message: '8'},
            { validator: validatePass, trigger: 'blur' }
          ],
          phone: [
            { validator: validPhone, trigger: 'blur' }
          ],
          BusiCode: [
            { validator: validBusiCode, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        
        this.$refs[formName].validate((valid) => {
          
          if (valid) {
            // dispatchPromise                         
                this.$store.dispatch('login', this.loginForm.phone).then(() => {
                    this.$notify({
                        type: 'success',
                        message: ',' + this.loginForm.phone + '!',
                        duration: 3000
                    })
                   
                })
            this.$router.replace({
              name: 'Index'
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
    
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.el-form-item.is-required >>> .el-form-item__label:before {
    content: '';
}
</style>



