<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <el-form :model="registerForm" status-icon :rules="rules2" ref="registerForm" label-width="100px" class="demo-ruleForm">
      <el-form-item label="" prop="organname">
        <el-input v-model="registerForm.organname" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="xincode">
        <el-input v-model="registerForm.xincode" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="operatorName">
        <el-input v-model="registerForm.operatorName" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="IdCardN">
        <el-input v-model.number="registerForm.IdCardN" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model.number="registerForm.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="verifycode">
        <el-input v-model="registerForm.verifycode" placeholder="" class="identifyinput"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="sendcode" @click="getvcode('registerForm')" :disabled="isDisabled">
            {{identifyCode}}
        </el-button>
      </el-form-item>
      <el-form-item label="" >
        <el-input placeholder=""></el-input>
      </el-form-item>
       <el-form-item label="" >
        <el-input type="password" autocomplete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="pass">
        <el-input type="password" v-model="registerForm.pass" autocomplete="off" placeholder="8"></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model.number="registerForm.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-upload
          class="avatar-uploader"
          action="https://jsonplaceholder.typicode.com/posts/"
          :show-file-list="false"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="imageUrl" :src="imageUrl" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item prop="checked">
        <el-checkbox v-model="checked" > </el-checkbox>
      </el-form-item>
      <el-form-item>
        <router-link tag="li" to="/register">
           <el-button @click="resetForm('registerForm')"></el-button>
        </router-link>
        <el-button type="primary" @click="submitForm('registerForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { isValidMobile, isValidxincode, isValidverifycode, isValidIdCardNo } from '@/util/extends.js'
export default {
    data() {
      var validPhone=(rule, value,callback)=>{
        if (!value){
            callback(new Error(''))
        }else  if (!isValidMobile(value)){
          callback(new Error('11'))
        }else {
            callback()
        }
      }
      var validxincode=(rule, value,callback)=>{
          if (!value){
              callback(new Error(''))
          }else  if (!isValidxincode(value)){
            callback(new Error('18'))
          }else {
              callback()
          }
      }
      var validIdCardNo=(rule, value,callback)=>{
          if (!value){
              callback(new Error(''))
          }else  if (!isValidIdCardNo(value)){
            callback(new Error(''))
          }else {
              callback()
          }
      }
      var validverifycode=(rule, value,callback)=>{
          if (!value){
              callback(new Error(''))
          }else  if (!isValidverifycode(value)){
            callback(new Error('4'))
          }else {
              callback()
          }
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.registerForm.checkPass !== '') {
            this.$refs.registerForm.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.registerForm.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      var validatechecked = (rule, checked, callback) => {
        console.log(this.checked)
        if (this.checked == false) {
          callback(new Error(' '));
        }  else {
          callback();
        }
      };
      return {
        msg: '',
        imageUrl: '',
        identifyCode: '',
        isDisabled: false,
				time: 60,
        checked: true,
        registerForm: {
          organname: '',
          pass: '',
          operatorName: '',
          IdCardNo: '',
          phone: '',
          xincode: '',
          verifycode: ''
        },
        rules2: {
          organname: [
            { required: true,message: '', trigger: 'blur' }
          ],
          operatorName: [
            { required: true,message: '', trigger: 'blur' }
          ],
          xincode: [
            {  validator: validxincode, trigger: 'blur' }
          ],
          IdCardNo: [
            {  validator: validIdCardNo, trigger: 'blur' }
          ],
          verifycode: [
            {validator: validverifycode, trigger: 'blur'}
          ],
          pass: [
            {required: true, min: 8, message: '8'},
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          phone: [
            { validator: validPhone, trigger: 'blur' }
          ],
          checked: [
            { validator: validatechecked, trigger: 'change'}
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let that = this
            this.$message({
              message: '',
              onClose() {
                that.$router.replace({
                  name: 'CompanyLogin'
                })
              }
             });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      getvcode(formName){
         this.$refs[formName].validateField('phone',(valid) => {
           console.log(valid)
          if (valid) {
           console.log('error submit!!');
          } else {
            
            this.sendMsg()
            return false;
          }
        });
        
      },
      sendMsg() { 
        let me = this;
        me.isDisabled = true;
        let interval = window.setInterval(function() {
          me.identifyCode = + me.time + '';
          --me.time;
          if(me.time < 0) {
            me.identifyCode = ''
            me.time = 60;
            me.isDisabled = false;
            window.clearInterval(interval);
          }
        }, 1000);
      },
      handleAvatarSuccess(res, file) {
        this.imageUrl = URL.createObjectURL(file.raw);
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error(' JPG !');
        }
        if (!isLt2M) {
          this.$message.error(' 2MB!');
        }
        return isJPG && isLt2M;
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.el-form-item.is-required >>> .el-form-item__label:before {
    content: '';
}
</style>


