<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2" label-width="100px" class="forget-ruleForm">
      <el-form-item label="" prop="xincode">
        <el-input v-model="ruleForm2.xincode" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="phone">
        <el-input v-model.number="ruleForm2.phone" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="verifycode">
        <el-input v-model="ruleForm2.verifycode" placeholder="" class="identifyinput"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button class="sendcode" @click="getvcode('ruleForm2')" :disabled="isDisabled">
            <span class="txt">{{identifyCode}}</span>
        </el-button>
      </el-form-item>
      <el-form-item label="" prop="pass">
        <el-input type="password" v-model="ruleForm2.pass" autocomplete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="checkPass">
        <el-input type="password" v-model="ruleForm2.checkPass" autocomplete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <router-link tag="li" to="/register">
           <el-button @click="resetForm('ruleForm2')"></el-button>
        </router-link>
        <el-button type="primary" @click="submitForm('ruleForm2')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { isValidMobile, isValidxincode, isValidverifycode } from '@/util/extends.js'
export default {
    data() {
      var validPhone=(rule, value,callback)=>{
        if (!value){
            callback(new Error(''))
        }else  if (!isValidMobile(value)){
          callback(new Error('11'))
        }else {
            callback()
        }
      }
      var validxincode=(rule, value,callback)=>{
          if (!value){
              callback(new Error(''))
          }else  if (!isValidxincode(value)){
            callback(new Error('18'))
          }else {
              callback()
          }
      }
      var validverifycode=(rule, value,callback)=>{
          if (!value){
              callback(new Error(''))
          }else  if (!isValidverifycode(value)){
            callback(new Error('4'))
          }else {
              callback()
          }
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
          }
          callback();
        }
      };
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if (value !== this.ruleForm2.pass) {
          callback(new Error('!'));
        } else {
          callback();
        }
      };
      return {
        msg: '',
        identifyCode: '',
        isDisabled: false,
				time: 60,
        ruleForm2: {
          pass: '',
          checkPass: '',
          phone: '',
          xincode: '',
          verifycode: ''
        },
        rules2: {
          xincode: [
            {  validator: validxincode, trigger: 'blur' }
          ],
          verifycode: [
            {validator: validverifycode, trigger: 'blur'}
          ],
          pass: [
            {required: true, min: 8, message: '8'},
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          phone: [
            { validator: validPhone, trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let that = this
            this.$message({
              message: '',
              onClose() {
                that.$router.replace({
                  name: 'CompanyLogin'
                })
              }
             });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      getvcode(formName){
         this.$refs[formName].validateField('phone',(valid) => {
           console.log(valid)
          if (valid) {
           console.log('error submit!!');
          } else {
            
            this.sendMsg()
            return false;
          }
        });
        
      },
      sendMsg() { 
					let me = this;
					me.isDisabled = true;
					let interval = window.setInterval(function() {
						me.identifyCode = + me.time + '';
						--me.time;
						if(me.time < 0) {
							me.identifyCode = ''
							me.time = 60;
							me.isDisabled = false;
							window.clearInterval(interval);
						}
					}, 1000);
				}
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.el-form-item.is-required >>> .el-form-item__label:before {
    content: '';
}
</style>



