<template>
    <div class="index">
        <Navheader></Navheader>
        <div class="Mess_content">
            <div class="mes_title"> LAWS AND REGULATIONS</div>
            <div class="law_list">
                <p class="law_tit"  @click.stop='open1'>1999</p>
                <p class="law_tit" @click.stop='open2'></p> 
                <p class="law_tit" @click.stop='open3'></p>
                <p class="law_tit" @click.stop='open4'>2013</p>
                <p class="law_tit" @click.stop='open5'>(2006)</p>
                <p class="law_tit" @click.stop='open6'></p>
                <p class="law_tit" @click.stop='open7'>2015</p>
                <p class="law_tit" @click.stop='open8'>91</p>
                <p class="law_tit" @click.stop='open9'>19</p>
                <p class="law_tit" @click.stop='open10' style="border:0;"> </p>
            </div>
        </div>  
    </div>
</template>
<script>
import Navheader from '@/components/Navheader'
export default {
  name: 'Index',
  data () {
    return {
      inforList: '',
      date:'',
      pageSize:20,
      pageNum:1,
      totalCount:1,
    }
  },
  components:{Navheader},
  methods: { 
    open1:function(){
        //window.location.href='static/19991001.pdf';
        window.open("static/pdf/199910011999.pdf",'_blank')
    },
    open2:function(){
        window.open("static/pdf/19951001.pdf",'_blank')
    },
    open3:function(){
        window.open("static/pdf/20011001.pdf",'_blank')
    },
    open4:function(){
        window.open("static/pdf/201403012013.pdf",'_blank')
    },
    open5:function(){
        window.open("static/pdf/20070601(2006).pdf",'_blank')
    },
    open6:function(){
        window.open("static/pdf/2005-10-27.pdf",'_blank')
    },
    open7:function(){
        window.open("static/pdf/201504242015.pdf",'_blank')
    },
    open8:function(){
        window.open("static/pdf/20130601.pdf",'_blank')
    },
    open9:function(){
        window.open("static/pdf/20040701.pdf",'_blank')
    },
    open10:function(){
        window.open("static/pdf/20170701.pdf",'_blank')
    }      
  },
  created(){
    window.scroll(0,0);
  }
}
</script>
<style scoped lang="stylus">
#zhichiBtnBox{
    display: none;
}
.index{
  width:100%;
  overflow:hidden;
  min-width:1200px;
 // position relative
}
.Mess_content
    padding 45px 18.75% 23px
    box-sizing border-box
    .mes_title
        color #B09A7D
        font-size 30px
        font-weight 600
        border-bottom 1px solid #CCCCCC
        padding 16px 0
    .law_tit
        color #383838
        font-size 18px
        padding 30px 0
        border-bottom 1px dashed #ccc
.law_tit:hover
    cursor pointer
</style>


