<template>
    <div class="index">
       <Navheader></Navheader>
        <div class="Mess_content">
            <!-- <div class="mes_title"> PROFESSIONAL INFORMATION</div> -->
            <div class="mes_title"> INDUSTRY REPORT</div>
            <!-- <div class="trem_M" @click="jump_detail()">
                <div class="trem_tit">
                    <img src="@/assets/index/shoufei.png" alt=""/>     
                    <p class="item_tit"></p>    
                </div>
                <p class="item_con">20194300</p>
                <p class="item_date">2018-11-02</p>
            </div> -->
            <!-- <div class="trem_M" v-for="(item,index) in inforList" @click="jump_detail($event)" :index='index'> -->
             <div class="trem_M" v-for="(item,index) in inforList" >
                    <div class="trem_tit"  @click="openLogin">
                        <img src="@/assets/index/shoufei.png" alt=""/>     
                        <p class="item_tit">{{item.F_DATASOURCE}}</p>    
                    </div>
                <p class="item_con item_con_M">{{item.F_CONTENT}}</p>
                <p class="item_date">{{item.F_DATE}}</p>
            </div>
           <!-- <p class="seemore"> <i class="el-icon-arrow-down"></i><span></span></p> -->
           <p class="see_btn" @click="openLogin"></p>
        </div>
    </div>
</template>
<script>
import axios from 'axios'
import Navheader from '@/components/Navheader'
import { MessageBox } from 'element-ui';
export default {
  name: 'Index',
  data () {
    return {
      msg: 'Welcome to Your  A1',
      inforList:[
          {F_DATASOURCE: "", F_DATE: '2019.03.27',F_CONTENT: "315"},
          {F_DATASOURCE: "2019", F_DATE: '2019.03.26',F_CONTENT: ""},
          {F_DATASOURCE: "", F_DATE: '2019.03.22',F_CONTENT: "2019..."},
          //{F_DATASOURCE: "", F_DATE: '2019.03.08',F_CONTENT: "8090"},
          //{F_DATASOURCE: "2019", F_DATE: '2019.02.21',F_CONTENT: "2019.2.20GPLP2019panel"},
         // {F_DATASOURCE: "1CPI", F_DATE: '2019.02.15',F_CONTENT: "CPI0.5%9.1%2.3%2.0%1.8%1.7%0.5%CPI0.32"}
        ]
    }
  },
  components:{Navheader},
  mounted () {
    //this.changeCai() 
    //this.enterJin()     
  },
  methods: {   
    openLogin (){
        const h = this.$createElement;
        this.$msgbox({
          title: '',
          message: h('div', null, [
            h('span', null, ' '),
            h('input', { style: 'color: #666',class:'span_inp' }),
            h('span', { style: 'padding-right: 13px',class:'span_p'}, ' '),
            h('input', { style: 'margin-bottom: 13px',class:'span_inp' }),
            h('p', { style: 'margin-top: 15px' ,class:'p_l'}, '/10 '),
          ]),
          showCancelButton: true,
          showConfirmButton: false,
          confirmButtonText: '',
          cancelButtonText: '',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
    },
    changeCai (){
      $(".caifu_lists").hover(function(){
          $(this).find(".lists_title").hide();
           $(this).find(".lists_detail").show();
      },function(){
          $(this).find(".lists_title").show();
           $(this).find(".lists_detail").hide();
      })
    },
    jump_detail(event){
       // console.log(this.inforList[0].F_CONTENT)
        var index=event.currentTarget.getAttribute('index');//
       // console.log(index)
        //console.log(this.inforList[index].F_CONTENT)
        if(!this.inforList[index].F_CONTENT2 == false){
            this.$router.push({
                path:'/detail',
                name:'detail',
                query:{
                    title:encodeURIComponent(this.inforList[index].F_DATASOURCE),
                    date: encodeURIComponent(this.inforList[index].F_DATE),
                    content:encodeURIComponent(this.inforList[index].F_CONTENT),
                    content2:encodeURIComponent(this.inforList[index].F_CONTENT2),
                    content3:encodeURIComponent(this.inforList[index].F_CONTENT3),
                    content4:encodeURIComponent(this.inforList[index].F_CONTENT4)
                }
            })    
        }else{
            this.$router.push({
                path:'/detail',
                name:'detail',
                query:{
                    title:encodeURIComponent(this.inforList[index].F_DATASOURCE),
                    date: encodeURIComponent(this.inforList[index].F_DATE),
                    content:encodeURIComponent(this.inforList[index].F_CONTENT)
                }
            }) 
        }
        
    }       
  },
  created(){
     // this.getData()
  }
}
</script>
<style scoped lang="stylus">
.Mess_content .seemore{
  color: rgb(176, 154, 125);
  text-align: center;
  cursor: pointer;
}
.see_btn
    width 160px
    height 40px
    line-height 40px
    border 1px solid #b09a7d
    margin 50px auto 30px
    color #333
    border-radius 40px
    text-align center
    font-size 14px
.see_btn:hover
    cursor pointer
.span_inp{
  width: 85%;
  border: 1px solid #ced4da;
  border-radius: .25rem;
  margin-top: 23px;
  padding: 8px 0;
  color: #666
}
.p_l{
  opacity: 0.5;
}
#zhichiBtnBox{
    display: none;
}
.index{
  width:100%;
  overflow:hidden;
  min-width:1200px;
 // position relative
}
.Mess_content
    padding 45px 18.75% 23px
    box-sizing border-box
    .mes_title
        color #B09A7D
        font-size 30px
        font-weight 600
        border-bottom 1px solid #CCCCCC
        padding 16px 0
.trem_M
    border-bottom 1px dashed #ccc
    padding-bottom 12px
    .trem_tit
        overflow hidden
        padding 20px 0 10px
        img
            float left
            height 19px
        .item_tit
            color #383838
            font-size 18px
            font-weight 600
            padding-left 8px
            float left
            width 80%
            display -webkit-box
            overflow hidden
            white-space normal !important
            text-overflow ellipsis
            word-wrap break-word
            -webkit-line-clamp 1
            -webkit-box-orient vertical
    .item_con
        color #383838
        font-size 15px
        line-height 1.8
        display -webkit-box
        overflow hidden
        white-space normal !important
        text-overflow ellipsis
        word-wrap break-word
        -webkit-line-clamp 2
        /* autoprefixer: off */
        -webkit-box-orient vertical
        /* autoprefixer: on */
    .item_date    
        text-align right
        color #888888
        font-size 14px
        padding-top 15px
.trem_M:last-of-type
    border none
.trem_M:hover
    cursor pointer
</style>


