import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Register from '@/components/Register'
import ForgetPass from '@/components/ForgetPass'
import CompanyLogin from '@/components/CompanyLogin'
import product from '@/components/product'
import sever from '@/components/sever'
import ours from '@/components/ours'
import Message from '@/components/Message'
import messDetail from '@/components/Message/messDetail'
import Information from '@/components/Information'
import detail from '@/components/Information/detail'
import Laws from '@/components/Laws'
import NotFound from '@/components/NotFound'
import InvestorEduc from '@/components/InvestorEduc'//
import InvestorDetail from '@/components/InvestorEduc/InvestorDetail'//
Vue.use(Router)

export default new Router({
   mode: 'history',
  // base: '/dist/',             //
  // 	scrollBehavior: () => ({ y: 0 }),
  routes: [
    {
      path: '/',
      component: Index,
      redirect:'Index',
      meta: {
        title: '-',
        keepAlive: false // 
      }
    },{
      path: '/Index',
      name: 'Index',
      component: Index,
      meta: {
        title: '-',
        keepAlive: false // 
      }
    },{
      path:'/Register',
      name: 'Register',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:Register
    },{
      path:'/ForgetPass',
      name: 'ForgetPass',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:ForgetPass
    },{
      path:'/CompanyLogin',
      name: 'CompanyLogin',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:CompanyLogin
    },{
      path:'/product',
      name: 'product',
      meta: {
        title: '|',
        keepAlive: false // 
      },
      component:product
    },{
      path:'/sever',
      name: 'sever',
      meta: {
        title: '|',
        keepAlive: false // 
      },
      component:sever
    },{
      path:'/ours',
      name: 'ours',
      meta: {
        title: '|',
        keepAlive: false // 
      },
      component:ours
    },{
      path:'/Message',
      name: 'Message',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:Message
    },{
      path:'/Information',
      name: 'Information',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:Information
    },{
      path:'/detail',
      name: 'detail',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:detail
    },{
      path:'/messDetail',
      name: 'messDetail',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:messDetail
    },{
      path:'/Laws',
      name: 'Laws',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:Laws
    },{
      path:'/InvestorEduc',
      name: 'InvestorEduc',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:InvestorEduc
    },{
      path:'/InvestorDetail',
      name: 'InvestorDetail',
      meta: {
        title: '',
        keepAlive: false // 
      },
      component:InvestorDetail
    },
   { path: '*', component: NotFound }
  ]
})



